	module.exports = {"data":{"markdownRemark":{"html":"<p>与日本国际论坛的日本学者一起吃早饭，在意大利餐厅。期间，谈了一些问题，归纳起来是：</p>\n<p>最近发生的法务大臣永野关于\"南京大屠杀\"态度的事件，本质上反映了一些日本政治家对这个问题的看法，但是迫于国际社会的压力，执政的联合政权不能不作出让步，让永野下台。一位永野的朋友说，永野是落入了日本一些反对他的记者设的圈套里了，记者故意让永野说了这样的话，永野说的是日本军在南京杀的人那么多是一个虚构，但是承认\"南京大屠杀\"。报纸报道说是永野否定了\"南京大屠杀\"本身。这也可能是一种解释的说法。不论怎么说，这件事情反映出不少日本高层人士对这个问题没有深刻认识。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0090.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0090.md","frontmatter":{"title":"5月15日 星期日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0090.md absPath of file >>> MarkdownRemark"}}

